.navbar {
  width: 100%;
  max-width: 100%;
  background: rgba(1, 8, 45, 0.8);
  height: 70px;
  display: flex;
  -webkit-transition: 0.3s all;
  transition: 0.3s all;
  position: fixed;
  left: 0;
  padding: 5px;
  top: 0;
  justify-content: space-between;
  z-index: 999; }
  @media screen and (max-width: 320px) {
    .navbar {
      justify-content: center; } }
  .navbar .navbar-left {
    display: flex;
    align-items: center;
    padding-left: 5px; }
    .navbar .navbar-left .navbar-logo {
      width: 70px;
      margin-right: 15px; }
      .navbar .navbar-left .navbar-logo img {
        width: 100%; }
    @media screen and (max-width: 320px) {
      .navbar .navbar-left .navbar-logo {
        display: none; } }
    .navbar .navbar-left .navbar-links {
      display: flex;
      -webkit-margin-before: 0;
              margin-block-start: 0;
      -webkit-margin-after: 0;
              margin-block-end: 0;
      list-style-type: none;
      -webkit-margin-start: 0px;
              margin-inline-start: 0px;
      -webkit-margin-end: 0px;
              margin-inline-end: 0px;
      -webkit-padding-start: 0;
              padding-inline-start: 0; }
      .navbar .navbar-left .navbar-links .navbar-link {
        color: #E9275B;
        text-decoration: none;
        -webkit-transition: 0.3s all;
        transition: 0.3s all;
        margin-right: 10px; }
        .navbar .navbar-left .navbar-links .navbar-link:hover, .navbar .navbar-left .navbar-links .navbar-link.active {
          background: #ffff66;
          padding: 10px;
          border-radius: 25px;
          color: #01082d; }
  .navbar .navbar-right {
    display: flex;
    padding-right: 25px;
    align-items: center; }
    .navbar .navbar-right .navbar-social-media {
      -webkit-margin-before: 0;
              margin-block-start: 0;
      -webkit-margin-after: 0;
              margin-block-end: 0;
      list-style-type: none;
      -webkit-margin-start: 0px;
              margin-inline-start: 0px;
      -webkit-margin-end: 0px;
              margin-inline-end: 0px;
      -webkit-padding-start: 0;
              padding-inline-start: 0;
      display: flex; }
      .navbar .navbar-right .navbar-social-media .navbar-link {
        color: #E9275B;
        text-decoration: none;
        font-size: 18px;
        -webkit-transition: 0.3s all;
        transition: 0.3s all;
        margin-right: 10px; }
        .navbar .navbar-right .navbar-social-media .navbar-link:hover {
          color: #ffff66; }

