    margin-bottom: -5px;
    height: 50vh;
    min-height: 50vh;
    width: 100vw; }
  .header .header-logo {
    width: 250px;
    z-index: 99;
    height: 250px; }
  .header .down-button {
    position: absolute;
    bottom: 30px;
    left: auto;
    right: auto;
    z-index: 99;
    text-rendering: auto;
    color: #E9275B;
    font-size: 33px;
    background: none;
    border: none;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    text-align: center;
    cursor: pointer;
